const findOptions = (question) =>
  questionsOptions.find((opts) => opts.name === question);

const Question = (props) => {
  const {
    question: { id, question },
    num,
    visitNum,
    consultants,
    onChange,
  } = props;
  const options = findOptions(question);
  const label = `${num}. ${question}`;

  const handleChange = (event) => {
    if (event.currentTarget) {
      onChange(id, question, event.currentTarget.value);
    } else if (event.target) {
      onChange(id, question, event.target.value);
    } else {
      onChange(id, question, event);
    }
  };

  let input = <Input onChange={handleChange} />;
  if (options) {
    const { answers: defaultAnswers, answers2, multiple, other, text, type } = options;
    // ToDo     
    const answers = (visitNum === 2 && answers2) ? answers2 : defaultAnswers;
    if (answers && answers.length !== 0) {
      if (multiple) {
        //  
        input = (
          <Checkbox.Group onChange={handleChange}>
            {answers.map((answer, index) => (
              <div>
                <Checkbox value={answer} key={index}>
                  {answer}
                </Checkbox>
              </div>
            ))}
            {other && <Checkbox value=""></Checkbox>}
          </Checkbox.Group>
        );
      } else {
        //   
        input = (
          <Radio.Group onChange={handleChange}>
            {answers.map((answer, index) => (
              <Radio
                value={answer}
                key={index}
                style={{ display: "block", marginLeft: 0 }}
              >
                {answer}
              </Radio>
            ))}
            <Radio value="" key="" style={{ display: "block", marginLeft: 0 }}>
               
            </Radio>
          </Radio.Group>
        );
      }
    }
    if (type === "city") {
      // 
      input = (
        <Select placeholder="" onChange={handleChange}>
          <Select.Option value=""> </Select.Option>
          <Select.Option value=""></Select.Option>
          <Select.Option value="-">-</Select.Option>
          <Select.Option value=" "> </Select.Option>
        </Select>
      );
    }
    if (type === "consultant") {
      // 
      input = (
        <Select placeholder="" onChange={handleChange}>
          <Select.Option value=""> </Select.Option>
          {consultants.map((consultant) => (
            <Select.Option value={consultant}>{consultant}</Select.Option>
          ))}
        </Select>
      );
    }
    if (text) {
      //  
      input = <Input.TextArea rows={8} onChange={handleChange} />;
    }
  }

  return (
    <Card title={label}>
      <Form.Item name={id}>{input}</Form.Item>
      {options && options.other && (
        <Form.Item name={`${id}-other`}>
          <Input />
        </Form.Item>
      )}
    </Card>
  );
};

export const PatientVisitQuestionnaire = (props) => {
  const { patientVisit, questions } = props;
  const { id, patient, visit, patient_visit_answers, status, date } =
    patientVisit;
  const { openNotification } = useNotification();

  const [city, setCity] = useState(null);
  const [users, setUsers] = useState([]);
  const [consultants, setConsultants] = useState([]);

  const questionsWithAnswers = questions.map((question) => {
    const answer = patient_visit_answers.find(
      (ans) => ans.questionId === question.id
    );
    return {
      ...question,
      ...(answer && { answer: answer.answer, otherAnswer: answer.other }),
    };
  });

  const initialValues = {
    status,
    date: date ? moment(date) : moment(),
  };
  questionsWithAnswers.forEach((qstn) => {
    const { id, question, answer, otherAnswer } = qstn;
    const options = findOptions(question);
    if (options) {
      const { answers, multiple, other } = options;
      if (answers && answers.length !== 0) {
        if (multiple) {
          initialValues[id] = answer ? answer.split(", ") : [];
        } else {
          initialValues[id] = answer ?? "";
        }
      } else {
        initialValues[id] = answer ?? "";
      }
      if (other) {
        initialValues[`${id}-other`] = otherAnswer ?? "";
      }
    } else {
      initialValues[id] = answer ?? "";
    }
  });

  const onFinish = (values) => {
    const { status, date, ...answers } = values;
    postPatientVisit(id, {
      status,
      date,
      values: answers,
    })
      .then(() => {
        openNotification(" ");
      })
      .catch(() => {
        openNotification("    ");
      });
  };

  const onChangeQuestion = (id, question, value) => {
    const options = findOptions(question);
    if (options) {
      const { type } = options;
      if (type === "city") {
        setCity(value);
      }
    }
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  useEffect(() => {
    findAllUsersForForms().then((res) => setUsers(res.data));
  }, []);

  useEffect(() => {
    let alias = "";
    if (city === "") {
      alias = "moscow";
    } else if (city === "-") {
      alias = "spb";
    } else if (city === " ") {
      alias = "nn";
    }
    setConsultants(
      users
        .filter((user) => {
          return city === "" || user.city === alias;
        })
        .map((user) => user.appointment)
    );
  }, [city, users]);

  return (
    <Form
      name="basic"
      layout="vertical"
      onFinish={onFinish}
      onFinishFailed={onFinishFailed}
      initialValues={initialValues}
      autoComplete="off"
    >
      <Typography.Title level={3}>
        {patient.code} ({visit.name})
      </Typography.Title>
      <Card title="">
        <Form.Item name="status">
          <Select>
            <Select.Option value={0}></Select.Option>
            <Select.Option value={1}></Select.Option>
            <Select.Option value={2}></Select.Option>
          </Select>
        </Form.Item>
      </Card>
      <Card title="">
        <Form.Item name="date">
          <DatePicker format={"DD.MM.YYYY"} />
        </Form.Item>
      </Card>

      <br />
      <Typography.Title level={3}></Typography.Title>
      {questionsWithAnswers.map((question, index) => (
        <Question
          question={question}
          num={index + 1}
          visitNum={visit.num}
          onChange={onChangeQuestion}
          consultants={consultants}
          key={question.id}
        />
      ))}
      <Card>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Card>
    </Form>
  );
};
